import React, { useState, useRef, useEffect } from 'react';
import * as $ from './MyPageItemView.js';
import { getUser, getUserMe, EditUserInfo, DeleteUser } from 'src/api/User';
import { useQuery } from 'react-query';
import { Link, useHistory } from 'react-router-dom';
import { ReactComponent as LeftArrow } from '../assets/svg/ic_arrow_left.svg';

const UserSetting = () => {
  // const { data: user } = useQuery(['getUser'], getUser);
  const [user, setUser] = useState(null);
  const [focus, setFocus] = useState(false);
  const [inputs, setInputs] = useState({
    nickName: '',
    originalPassword: '',
    password: '',
    passwordConfirm: ''
  });
  const { nickName, originalPassword, password, passwordConfirm } = inputs;

  const inputRef = useRef('');
  const history = useHistory();
  const [changePw, setChangePw] = useState(false);

  useEffect(() => {
    setChangePw(false);
    getUserMe()
      .then((data) => {
        setUser(data);
      })
      .catch((err) => {
        console.log(err, 'err');
      });
  }, []);

  useEffect(() => {
    if (focus) {
      inputRef.current.focus();
    }
  }, [focus]);

  useEffect(() => {
    setFocus(false);
  }, [user]);

  const handleFocus = () => {
    setFocus(!focus);
  };

  const editInfo = () => {
    if (nickName) {
      EditUserInfo({ nickName })
        .then((data) => {
          setUser(data);
        })
        .catch((err) => {
          console.log(err, 'err');
        });
    } else {
      alert(' .');
      return;
    }
  };

  const onChangeInput = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value
    });
  };

  const changePassword = () => {
    if (!originalPassword) {
      alert('  .');
    } else if (!password) {
      alert('  .');
    } else if (!passwordConfirm) {
      alert('    .');
    } else if (password !== passwordConfirm) {
      alert('  .');
    } else {
      EditUserInfo({ originalPassword, password })
        .then((data) => {
          console.log(data, 'dataaaaa');
        })
        .catch((err) => {
          console.log(err, 'err');
        });
    }
    return;
  };

  const handleDelete = () => {
    DeleteUser();
    localStorage.removeItem('accessToken');
    localStorage.removeItem('refreshToken');
    history.push('/');
    window.location.reload();
    alert(' .');
  };

  return (
    <>
      {changePw ? (
        <$.FlexDiv direction={'column'}>
          <$.SettingBox>
            <LeftArrow onClick={() => setChangePw(false)} style={{ cursor: 'pointer' }} />
            <$.SettingTitle> </$.SettingTitle>

            <$.SettingLabel> </$.SettingLabel>
            <$.SettingInfo>
              <$.Input name="originalPassword" onChange={onChangeInput} value={originalPassword} type="password" />
            </$.SettingInfo>

            <$.SettingLabel> </$.SettingLabel>
            <$.SettingInfo>
              <$.Input name="password" onChange={onChangeInput} value={password} type="password" />
            </$.SettingInfo>

            <$.SettingLabel>  </$.SettingLabel>
            <$.SettingInfo>
              <$.Input name="passwordConfirm" onChange={onChangeInput} value={passwordConfirm} type="password" />
            </$.SettingInfo>

            <$.SettingButton onClick={changePassword}>
              <$.SettingButtonText></$.SettingButtonText>
            </$.SettingButton>
          </$.SettingBox>
        </$.FlexDiv>
      ) : (
        <$.FlexDiv direction="column">
          <$.SettingBox>
            <$.SettingTitle></$.SettingTitle>

            <$.SettingLabel></$.SettingLabel>
            <$.SettingInfoBox>
              <$.SettingInfo>{user?.name}</$.SettingInfo>
            </$.SettingInfoBox>

            <$.SettingLabel></$.SettingLabel>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <$.SettingInfoBox focus={focus}>
                <$.SettingInfo>
                  {focus ? (
                    <$.Input name="nickname" onChange={onChangeInput} value={nickName} ref={inputRef} />
                  ) : (
                    <>{user?.nickName}</>
                  )}
                </$.SettingInfo>
              </$.SettingInfoBox>
              {focus ? (
                <>
                  <$.SettingButton onClick={editInfo} c="#e64980" w={78}>
                    <$.SettingButtonText></$.SettingButtonText>
                  </$.SettingButton>
                  <$.SettingButton onClick={handleFocus} w={54}>
                    <$.SettingButtonText></$.SettingButtonText>
                  </$.SettingButton>
                </>
              ) : (
                <$.SettingButton onClick={handleFocus} w={105}>
                  <$.SettingButtonText></$.SettingButtonText>
                </$.SettingButton>
              )}
            </div>

            <$.SettingLabel></$.SettingLabel>
            <$.SettingInfoBox>
              <$.SettingInfo>{user?.email}</$.SettingInfo>
            </$.SettingInfoBox>
          </$.SettingBox>

          <$.SettingBox>
            <$.SettingTitle> </$.SettingTitle>
            <$.ImgBox></$.ImgBox>
            <div>
              <$.SettingButton>
                <$.SettingButtonText> </$.SettingButtonText>
              </$.SettingButton>
              <$.SettingButton w={54}>
                <$.SettingButtonText></$.SettingButtonText>
              </$.SettingButton>
            </div>
          </$.SettingBox>
          <$.SettingBox>
            <$.SettingTitle></$.SettingTitle>
            <$.SettingInfo>       .</$.SettingInfo>
            <$.SettingButton onClick={() => setChangePw(true)}>
              <$.SettingButtonText> </$.SettingButtonText>
            </$.SettingButton>
          </$.SettingBox>
          <$.SettingBox>
            <$.SettingButton onClick={handleDelete}>
              <$.SettingButtonText> </$.SettingButtonText>
            </$.SettingButton>
          </$.SettingBox>
        </$.FlexDiv>
      )}
    </>
  );
};

export default UserSetting;
